body {
    color: $dark-grey-1;
    font-size:1.6rem;
    font-family: $Jsans;
    font-weight: 400;
    line-height:1.5;
}
.page-title {
    font-size:3rem;
    font-weight:300;
    
    &__subtitle {
        font-size:2.4rem;
        font-weight:300;
        display:inline-block;
    }
}

.section {
    
    &__title {
        font-size:5rem;
        font-weight:300;
        text-align:center;
    }
    &__text{
        font-size:2.2rem;
        text-align:center;
        color:$dark-grey-2;
    }
}

    
